.dots {
  height: 15px;
  display: flex;
  gap: 15px;
  overflow: visible;
}
.dot {
  width: 10px;
  height: 10px;
  background-color: #232b2b;
  box-shadow: 0px 0px 3px 1px #bababa;
  border-radius: 100%;
  transition: all 0.3s;
}

.brightdot {
  @extend .dot;
  background-color: #9a9a9a;
}
